.loading-table-header {
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
}

.loading-table-item {
  width: 30%;
  height: 10px;
  background: #eee;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.loading-table-item + .loading-table-item  {
  margin-left: 40px;
}

.loading-table-row {
  border-top: 1px solid #eee;
  padding: 5px;
  display: -ms-flexbox;
  display: flex;
  min-height: 20px;
  -webkit-animation: pulse-bg 1s infinite;
          animation: pulse-bg 1s infinite;
}

.loading-table-row:nth-child(2) {-webkit-animation-delay: .25s;animation-delay: .25s; }
.loading-table-row:nth-child(3) {-webkit-animation-delay: .5s;animation-delay: .5s; }
.loading-table-row:nth-child(4) {-webkit-animation-delay: .75s;animation-delay: .75s; }

@-webkit-keyframes pulse-bg {
  0% { background: #fff; }
  10% { background: #eee; }
  100% { background: #fff; }
}

@keyframes pulse-bg {
  0% { background: #fff; }
  10% { background: #eee; }
  100% { background: #fff; }
}




